.home{

    .homeContent {
         background: linear-gradient(115deg, $color-1 50%, transparent 100%);
         min-height: $minHeight;
         width: 100%;
         min-height: 500px;
         border-radius: $borderRadius;
         position: relative;
         overflow: hidden;

         &::after {
             content: '';
             position: absolute;
             height: 100%;
             width: inherit;
             background: url('../../../images/imgPerso.jpg') no-repeat center/cover;
             z-index: -1;
             filter: brightness(30%);
         }

         .content {
             position: absolute;
             top: 20%;
             left: 13%;
             font-family: $font-1;

             h1{
                 font-size: 2.5rem;
                 font-family: $font-1;
                 color: $color-5;
                 text-transform: uppercase;
                 text-shadow: 1px 4px 1px $black;
             }

             h2 {
                font-size: 1.5rem;
                color: $color-3;
                padding: 1.5rem 0 7rem;
                margin-left: 3rem;
                text-shadow: 1px 4px 1px $black;
             }
            //  .profil {
            //      text-align: center;
            //     font-size: 1.5rem;
            //     background: $color-3;
            //     width: 450px;
            //     padding: 1rem 2rem;
            //     margin-bottom: 4rem;
            //     border-radius: $borderRadius;
            //     box-shadow: 0 5px 1px rgb(15, 15, 15);
            //     transition: 0.2s ease;

            //     // .monProfil{
            //     //     display: none;
            //     // }
      
            //     &:hover {
            //       background: $color-4;
            //     }
            //   }

             .pdf {
                 
                a {
                  font-size: 1.5rem;
                  background: $color-3;
                  margin-left: 4rem;
                  padding: 1rem 2rem;
                  border-radius: $borderRadius;
                  box-shadow: 0 5px 1px rgb(15, 15, 15);
                  transition: 0.2s ease;
        
                  &:hover {
                    background: $color-4;
                  }
                }
              }
         }
    }
}

@media screen and (max-width: 850px){

    .homeContent {
        margin: 0 !important;
    }
    
}

@media screen and (max-width: 500px){

    .homeContent {
        &::after {
            background: url('../../../images/imgPerso.jpg') no-repeat center/cover !important;
        }

        h1 {
            font-size: 2rem !important;
        }

        h2 {
            font-size: 1.5rem !important;
        }
    }
    
}


